export function AppLayout({ children, userRole }: AppLayoutProps) {
  const router = useRouter();

  const handleLogout = () => {
    // Clear authentication state (e.g., remove token from localStorage)
    localStorage.removeItem('userRole');
    localStorage.removeItem('userId'); // Also remove userId
    // Redirect to login page
    router.push('/login');
  };

  // Determine sidebar content based on user role
  const sidebarContent = userRole === "admin" ? <AdminSidebar /> : <EmployeeSidebar />;

  return (
    <SidebarProvider defaultOpen>
      <Sidebar>
        <SidebarHeader className="p-4 border-b border-sidebar-border">
          <h1 className="text-xl font-semibold text-sidebar-primary-foreground">Gaea Realty and Consultants India pvt. ltd.</h1>
           <span className="text-sm text-sidebar-foreground/80 capitalize">{userRole} Dashboard</span>
        </SidebarHeader>
        <SidebarContent className="flex-1 overflow-y-auto p-0">
          {sidebarContent}
        </SidebarContent>
        <SidebarFooter className="p-4 border-t border-sidebar-border">
          <Button variant="ghost" className="w-full justify-start text-sidebar-foreground hover:bg-sidebar-accent" onClick={handleLogout}>
            <LogOut className="mr-2 h-4 w-4" />
            Logout
          </Button>
        </SidebarFooter>
      </Sidebar>
      <SidebarInset className="bg-background">
        <header className="sticky top-0 z-10 flex h-14 items-center justify-between gap-4 border-b bg-card px-4 sm:static sm:h-auto sm:border-0 sm:bg-transparent sm:px-6">
           <SidebarTrigger className="sm:hidden" /> {/* Show trigger only on mobile */}
           <div className="flex items-center gap-4">
              {/* Placeholder for potential header actions like search or user profile */}
           </div>
        </header>
        <main className="flex-1 p-4 sm:px-6 sm:py-0">{children}</main>
      </SidebarInset>
    </SidebarProvider>
  );
}
